<template>
  <div style="margin-bottom:4.3rem;">
    <mt-loadmore
      :top-method="loadData"
      :bottom-method="loadMore"
      :auto-file="false"
      ref="loadmore"
      v-for="(item,index) in list"
      :key="index"
    >
      <!-- 视频 -->
      <router-link class="watch" v-for="(s,index) in item.watch" :key="'info1-'+index" :to="s.path">
        <div class="wenzi fl-ex al-it ju-be">
          <h3>{{s.h3}}</h3>
          <p>{{s.p}}</p>
        </div>
        <div class="audio">
          <video :src="s.mp" controls preload loop></video>
        </div>
      </router-link>
      <!-- 图片 -->
      <router-link class="watch" v-for="(s,index) in item.pic" :key="'info2-'+index" :to="s.path">
        <div class="wenzi fl-ex al-it ju-be">
          <h3>{{s.h3}}</h3>
          <p>{{s.p}}</p>
        </div>
        <div class="img">
          <img :src="s.pic" alt="图片" />
        </div>
      </router-link>
    </mt-loadmore>
    <div class="more">亲，没有更多了!</div>
  </div>
</template>

<script>
import { Loadmore } from "mint-ui";
export default {
  data() {
    return {
      list: [
        {
        watch:[
          {
             h3: "马吉孝同志在省级群团组织传达学习全国",
             p: "2018-08-06",
             mp: "../../static/img/12.mp4",
             path:"/Build-Videoone"
          },
          
        ],
        pic:[
            {
               h3: "马吉孝同志在省级群团组织传达学习全国",
             p: "2018-08-06",
              pic:"../../static/img/16.png",
              path:'/Build-Picone'
            }
          ]
        }
      ],
      
    };
  },
  methods: {
    loadData() {
      this.allLoaded = false;
      this.bottomPullText = "上拉加载更多";
      拉取商家信息;
    },
    loadMore() {
      if (!this.allLoaded) {
        this.$axios.post();
      }
    }
  }
};
</script>

<style scoped>
.watch {
  width: 100%;
  height: 13rem;
}
.watch .wenzi {
  width: 95%;
  height: 2rem;
  margin: 0 auto;
  font-size: 0.82rem;
  color: black;
  font-weight: bold;
}
.watch .wenzi h3 {
  font-size: 0.72rem;
  color: black;
}
.watch .wenzi p {
  font-size: 0.6rem;
  color: #3f3f3f;
}
.watch .audio {
  width: 95%;
  height: 10.5rem;
  margin: 0 auto;
}
.watch .img {
  width: 95%;
  height: 10.5rem;
  margin: 0 auto;
}
.watch .img img {
  width: 100%;
  height: 10.5rem;
}
.watch .audio > video {
  width: 100%;
  height: 100%;
}
.more {
  width: 100%;
  height: 2rem;
  text-align: center;
  line-height: 2rem;
  color: #777777;
  font-size: 0.82rem;
}
</style>


